import { getStrLenValidateObj } from '@/common/validator/common.js';

export function getPasswordValidateRules(passwordRule) {
  const {
    minimumLength = 8,
    maximumLength = 255,
    minimumNumeric,
    minimumAlphabetic,
    minimumUpperCase,
    minimumSpecial,
    minimumDifference,
    specialList,
    allow2ContinuSameChar,
    caseSensitive,
  } = passwordRule;
  const rules = [];

  // 1、限制密码的位数
  rules.push(getStrLenValidateObj({ minLen: minimumLength, maxLen: maximumLength }));
  // 2、检查最少数字要求
  if (minimumNumeric) {
    rules.push(checkMinimumNumeric(minimumNumeric));
  }
  // 3、检查最少字母要求
  if (minimumAlphabetic) {
    rules.push(checkMinimumAlphabetic(minimumAlphabetic));
  }
  // 4、检查最少需要的大写字母
  if (minimumUpperCase && caseSensitive) {
    rules.push(checkMinimumUpperCase(minimumUpperCase));
  }
  // 5、检查最少的特殊字符要求
  if (minimumSpecial) {
    rules.push(checkMinimumSpecial(minimumSpecial, specialList));
  }
  // 6、密码去重后最少的字符数
  if (minimumDifference) {
    rules.push(checkMinimumDifference(minimumDifference));
  }
  // 7、是否允许出现连续字符
  if (allow2ContinuSameChar) {
    rules.push(checkAllow2ContinuSameChar());
  }

  return rules;
}

/* ------------------------------ minimumNumeric ------------------------------ */
function checkMinimumNumeric(minimumNumeric) {
  return {
    validator: (rule, value, callback) => {
      if (countNumeric(value) < minimumNumeric) {
        callback(new Error(`Minimum number of digits required: ${minimumNumeric}`)); // 校验不通过
      } else {
        callback();
      }
    },
    trigger: 'blur',
  };
}

function countNumeric(password) {
  let count = 0;
  for (let i = 0; i < password.length; i++) {
    let charCode = password[i].charCodeAt();
    if (isNumericChar(charCode)) {
      count++;
    }
  }
  return count;
}

/* ------------------------------ minimumAlphabetic ------------------------------ */
function checkMinimumAlphabetic(minimumAlphabetic) {
  return {
    validator: (rule, value, callback) => {
      if (countLetters(value) < minimumAlphabetic) {
        callback(new Error(`Minimum number of letters required: ${minimumAlphabetic}`)); // 校验不通过
      } else {
        callback();
      }
    },
    trigger: 'blur',
  };
}

function countLetters(password) {
  let count = 0;
  for (let i = 0; i < password.length; i++) {
    let charCode = password[i].charCodeAt();
    if (isUppercase(charCode) || isLowercase(charCode)) {
      count++;
    }
  }
  return count;
}

/* ------------------------------ minimumUpperCase ------------------------------ */
function checkMinimumUpperCase(minimumUpperCase) {
  return {
    validator: (rule, value, callback) => {
      if (countUpperCaseLetters(value) < minimumUpperCase) {
        callback(new Error(`Minimum number of capital letters required: ${minimumUpperCase}`)); // 校验不通过
      } else {
        callback();
      }
    },
    trigger: 'blur',
  };
}

function countUpperCaseLetters(password) {
  let count = 0;
  for (let i = 0; i < password.length; i++) {
    let charCode = password[i].charCodeAt();
    if (isUppercase(charCode)) {
      count++;
    }
  }
  return count;
}

/* ------------------------------ minimumSpecial ------------------------------ */
function checkMinimumSpecial(minimumSpecial, specialList) {
  return {
    validator: (rule, value, callback) => {
      if (countSpecialCharacters(value, specialList) < minimumSpecial) {
        callback(
          new Error(
            `Minimum number of special characters required: ${minimumSpecial}.  Special characters allowed in the password: ${specialList}`
          )
        ); // 校验不通过
      } else {
        callback();
      }
    },
    trigger: 'blur',
  };
}

function countSpecialCharacters(password, specialList) {
  let count = 0;
  for (let i = 0; i < password.length; i++) {
    let char = password[i];
    if (specialList.includes(char)) {
      count++;
    }
  }
  return count;
}

/* ------------------------------ minimumDifference ------------------------------ */
function checkMinimumDifference(minimumDifference) {
  return {
    validator: (rule, value, callback) => {
      let arr = [...new Set(value.split(''))];
      if (arr.length < minimumDifference) {
        callback(new Error(`At least ${minimumDifference} different characters are required`)); // 校验不通过
      } else {
        callback();
      }
    },
    trigger: 'blur',
  };
}

/* ------------------------------ allow2ContinuSameChar ------------------------------ */
function checkAllow2ContinuSameChar() {
  return {
    validator: (rule, value, callback) => {
      if (hasContinuSameChar(value)) {
        callback(new Error('Consecutive identical characters are not allowed')); // 校验不通过
      } else {
        callback();
      }
    },
    trigger: 'blur',
  };
}

function hasContinuSameChar(password) {
  let has = false;
  for (let i = 0; i < password.length - 1; i++) {
    if (password[i] === password[i + 1]) {
      has = true;
      break;
    }
  }
  return has;
}

function isNumericChar(charCode) {
  return charCode >= 48 && charCode <= 57;
}

function isUppercase(charCode) {
  return charCode >= 65 && charCode <= 90;
}

function isLowercase(charCode) {
  return charCode >= 97 && charCode <= 122;
}
