<template>
  <div class="force-changepwd-content">
    <div class="changepwd-title">
      <p class="huaweiSans_bold">{{ $i18n.t('auth.changePassword') }}</p>
    </div>
    <div v-if="!loginErrorMsg" class="force-change-tips">
      <span>
        <i>{{ operatorName }}</i> User
      </span>
      <span v-if="expireFlag === 'aboutToExpire'">
        {{ $t('auth.aboutToExpire') }}
      </span>
      <span v-else-if="expireFlag === 'hasExpire'">
        {{ $t('auth.yourPassword') }}
        <i class="error-text">{{ $t('auth.hasExpired') }}</i>
        {{ $t('auth.pleaseChangePassword') }}
      </span>
      <span v-else>
        {{ $t('auth.changePwdTips') }}
      </span>
    </div>

    <div v-if="loginErrorMsg.length > 0" class="error-tip-content">
      {{ loginErrorMsg }}
    </div>

    <div class="change-pwd-form">
      <el-form
        id="change-pwd-form"
        :model="changePwdForm"
        ref="changePwdForm"
        :rules="changePasswordRules"
        :style="{ 'padding-top': loginErrorMsg ? '32px' : '20px' }"
      >
        <el-form-item prop="oldPassword" :label="$i18n.t('auth.oldPassword')">
          <el-input
            id="oldPassword"
            size="small"
            type="password"
            v-model="changePwdForm.oldPassword"
            placeholder="Please enter"
            @keyup.native.enter="nextFocus($event)"
            @focus="initInputDom()"
          >
            <svg-icon v-if="oldPasswordCheck" slot="suffix" icon-class="success" class="success-icon"></svg-icon>
          </el-input>
        </el-form-item>

        <el-form-item prop="newPassword" :label="$i18n.t('auth.newPassword')">
          <el-input
            id="newPassword"
            size="small"
            type="password"
            v-model="changePwdForm.newPassword"
            @keyup.native.enter="nextFocus($event)"
            placeholder="Please enter"
            @focus="initInputDom('newPassword')"
          >
            <svg-icon v-if="newPasswordCheck" slot="suffix" icon-class="success" class="success-icon"></svg-icon>
          </el-input>
          <div v-show="showTips" class="newPassword-tips">
            {{ $t('home.newPwdTips', { obj: minimumLength + '~' + maximumLength }) }}
          </div>
        </el-form-item>

        <el-form-item prop="confirmPassword" :label="$i18n.t('auth.confirmPassword')">
          <el-input
            id="confirmPassword"
            size="small"
            type="password"
            v-model="changePwdForm.confirmPassword"
            placeholder="Please enter"
            @keyup.native.enter="nextFocus($event)"
            @focus="initInputDom()"
          >
            <svg-icon v-if="confirmPasswordCheck" slot="suffix" icon-class="success" class="success-icon"></svg-icon>
          </el-input>
        </el-form-item>
      </el-form>

      <el-button
        id="changePwdBtn"
        :disabled="!checkFormIptLth"
        class="login-btn"
        type="primary"
        v-preventReClick
        @click="submitForm('changePwdForm')"
        :loading="loginLoading"
      >
        {{ $i18n.t('auth.submit') }}
        <i class="el-icon-right el-icon--right"></i>
      </el-button>

      <el-button type="text" class="return-btn" @click="returnEvent"> Return > </el-button>
    </div>
  </div>
</template>

<script>
import { changePasswordOnFirstLogin, changePassword, passwordRule } from '../../api';
import { inputRequiredValidateObj } from '@/common/validator/common';
import { authenticate } from '@/views/home/api';
import { getPasswordValidateRules } from '@/views/auth/validator/password';
import cookie from '@/common/utils/cookie';
export default {
  name: 'ForceChangePassword',
  props: {
    operatorName: {
      type: String,
      default: '',
    },
    shortCode: {
      type: String,
      default: '',
    },
    expireFlag: {
      type: String,
      default: '',
    },
  },
  data() {
    let newPasswordVail = async (rule, value, callback) => {
      if (value) {
        if (value.length < this.minimumLength || value.length > this.maximumLength) {
          this.newPasswordCheck = false;
          this.showTips = false;
          return callback(new Error(`Please enter a number between ${this.minimumLength} and ${this.maximumLength}.`));
        } else {
          this.showTips = false;
          this.newPasswordCheck = true;
          callback();
        }
      } else {
        this.showTips = false;
        this.newPasswordCheck = false;
        callback(new Error('Please enter content'));
      }
    };
    let comfirmPassworldVail = (rule, value, callback) => {
      if (value !== this.changePwdForm.newPassword) {
        this.confirmPasswordCheck = false;
        callback(new Error('The two passwords are inconsistent.'));
      } else {
        this.confirmPasswordCheck = true;
        callback();
      }
      callback();
    };
    let oldPasswordVail = (rule, value, callback) => {
      if (value) {
        let data = {
          password: value,
          identityId: cookie.get('initiatorIdentityId'),
        };
        authenticate(data).then((res) => {
          if (res.header && res.header.success) {
            this.oldPasswordCheck = true;
            callback();
          } else {
            this.oldPasswordCheck = false;
            callback(new Error('Please Check The Password And Try Agin'));
          }
        });
      } else {
        this.oldPasswordCheck = false;
        callback(new Error('Please enter content'));
      }
    };
    return {
      loginErrorMsg: '',
      changePwdForm: {
        oldPassword: '',
        newPassword: '',
        confirmPassword: '',
      },
      changePasswordRules: {
        oldPassword: [{ validator: oldPasswordVail, trigger: 'blur' }],
        newPassword: [{ validator: newPasswordVail, trigger: 'blur' }],
        confirmPassword: [inputRequiredValidateObj, { validator: comfirmPassworldVail, trigger: 'blur' }],
      },
      oldPasswordCheck: false,
      newPasswordCheck: false,
      confirmPasswordCheck: false,
      showTips: false,
      loginLoading: false,
      isFirstTips: true,
      inputDoms: [],
      minimumLength: 8,
      maximumLength: 20,
    };
  },
  computed: {
    checkFormIptLth() {
      let allEnterValue = false;
      if (
        this.changePwdForm['oldPassword'].length > 0 &&
        this.changePwdForm['newPassword'].length > 0 &&
        this.changePwdForm['confirmPassword'].length > 0
      ) {
        allEnterValue = true;
      }
      return allEnterValue;
    },
  },
  created() {},
  methods: {
    returnEvent() {
      this.$router.go(-1);
    },

    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (this.changePwdForm.newPassword !== this.changePwdForm.confirmPassword) {
          this.loginErrorMsg = 'The passwords you entered do not match.';
          return;
        } else {
          this.loginErrorMsg = '';
          if (valid) {
            if (formName === 'changePwdForm') {
              if (this.expireFlag === 'aboutToExpire' || this.$route.query.redirectFlag === 'FLFMQAorPEFM') {
                this.expireChangePassword();
              } else {
                this.changePasswordAndlogin();
              }
            }
          } else {
            return false;
          }
        }
      });
    },

    changePasswordAndlogin() {
      this.loginLoading = true;
      let data = {
        newPassword: this.changePwdForm.newPassword,
        oldPassword: this.changePwdForm.oldPassword,
        confirmPassword: this.changePwdForm.confirmPassword,
        operatorName: this.operatorName,
        shortCode: this.shortCode,
        initiatorIdentityId: this.$route.query.initiatorIdentityId,
        receiverIdentityId: this.$route.query.initiatorIdentityId,
      };
      changePasswordOnFirstLogin(data)
        .then((res) => {
          if (res.header && res.header.success) {
            this.$store.dispatch('routerStore/setTopMenus').then(() => {
              this.$router.push({
                path: '/mainPage/identifierCenter/MyOrganization/MyOrganization/list',
                query: {
                  operatorName: this.operatorName,
                },
              });
            });
          } else {
            this.loginErrorMsg = res.header.responseDesc;
          }
        })
        .finally(() => {
          this.loginLoading = false;
        });
    },

    expireChangePassword() {
      this.loginLoading = true;
      let data = {
        newPwd: this.changePwdForm.newPassword,
        oldPwd: this.changePwdForm.oldPassword,
        confirmPassword: this.changePwdForm.confirmPassword,
        operatorName: this.operatorName,
        shortCode: this.$route.query.shortCode,
        initiatorIdentityId: this.$route.query.initiatorIdentityId,
        receiverIdentityId: this.$route.query.initiatorIdentityId,
      };
      changePassword(data)
        .then((res) => {
          if (res.header && res.header.success) {
            this.$message.success('Change password success!');
            this.$router.push({
              path: '/login',
            });
          } else {
            this.loginErrorMsg = res.header.responseDesc;
          }
        })
        .finally(() => {
          this.loginLoading = false;
        });
    },

    initInputDom(inputProp) {
      if (inputProp === 'newPassword' && this.isFirstTips) {
        passwordRule().then((res) => {
          if (res) {
            this.changePasswordRules.newPassword = [
              ...this.changePasswordRules.newPassword,
              ...getPasswordValidateRules(res),
            ];
            this.minimumLength = res.minimumLength;
            this.maximumLength = res.maximumLength;
          }
        });
        this.showTips = true;
        this.isFirstTips = false;
      }
      if (this.inputDoms.length != 0) {
        return;
      }
      let inputDoms = document.querySelectorAll('#change-pwd-form .el-input__inner');
      inputDoms.forEach((item, index) => {
        item.setAttribute('data-index', index);
      });
      this.inputDoms = inputDoms;
    },
    nextFocus(event) {
      let index = event.target.getAttribute('data-index');
      let nextIndex = parseInt(index) + 1;
      let length = this.inputDoms.length;
      if (nextIndex < length) {
        this.inputDoms[nextIndex].focus();
        return;
      }
      if (!this.loginLoading) {
        this.submitForm('changePwdForm');
      }
    },
  },
};
</script>

<style lang="scss" scoped>
