      margin-bottom: 14px;
    }
    /deep/ .el-form .el-form-item .el-form-item__label {
      margin-left: 0;
    }
    /deep/ .el-input--medium .el-input__inner {
      height: 48px;
    }
    /deep/ .el-form-item:last-child {
      margin-bottom: 8px;
    }
    /deep/.el-form-item__label {
      font-size: 14px;
      line-height: 26px;
    }
    /deep/ .el-input-group__append {
      line-height: normal;
      background-color: #fff;
    }
    /deep/ .el-form-item__label:before {
      content: '';
    }
    .return-btn {
      font-size: 14px;
      margin-left: 0px;
      margin-top: 12px;
      color: $--text_color--strong;
    }
    .success-icon {
      font-size: 15px;
      margin-right: 6px;
    }
    .newPassword-tips {
      font-size: 12px;
      color: $--text_color--secondary;
      line-height: 18px;
      margin-top: 4px;
    }
  }
  .error-tip-content {
    width: 100%;
    color: #f53f3f;
    background-color: #ffece8;
    border-radius: 4px;
    line-height: 24px;
    padding-left: 12px;
  }
  .changepwd-title {
    p {
      margin: 4px 0;
      color: $--text_color--emphasis;
      font-weight: Bold;
      font-size: 36px;
      margin-bottom: 12px;
    }
  }
  .force-change-tips {
    width: 100%;
    background-color: $--fill_color--light;
    border-radius: 4px;
    padding: 8px 12px;
    font-size: 14px;
    span {
      display: block;
      line-height: 20px;
      color: #8f8f8f;
      i {
        font-style: normal;
        color: $--status_color--notice;
      }
      .error-text {
        color: $--status_color--error;
      }
    }
  }
  .login-btn {
    width: 100%;
    height: 40px;
    margin-top: 18px;
    font-size: 16px;
    .el-icon--right {
      float: right;
      position: relative;
      top: -2px;
    }
  }
}
</style>
