<template>
  <div class="forget-pwd-container">
    <div class="forget-pwd-title">
      <p class="huaweiSans_medium">{{ $i18n.t('auth.welcomeTitle') }}</p>
      <p class="huaweiSans_bold">{{ $i18n.t('auth.forgetPwd') }}</p>
    </div>

    <div v-if="loginErrorMsg.length > 0" class="error-tip-content">
      {{ loginErrorMsg }}
    </div>

    <div class="forget-pwd-form">
      <el-form
        v-show="active === 1"
        :model="forgetPwdForm"
        id="forget-pwd-form"
        ref="forgetPwdForm"
        :rules="forgetPwdRules"
        :hide-required-asterisk="true"
      >
        <el-form-item prop="shortCode" :label="$i18n.t('auth.shortCode')">
          <el-input
            id="shortCode"
            size="small"
            type="text"
            v-model="forgetPwdForm.shortCode"
            placeholder="Please enter"
            @keyup.native.enter="nextFocus($event)"
            @focus="initInputDom()"
          />
        </el-form-item>

        <el-form-item prop="username" :label="$i18n.t('auth.userName')">
          <el-input
            id="username"
            size="small"
            v-model="forgetPwdForm.username"
            @keyup.native.enter="nextFocus($event)"
            placeholder="Please enter"
            @focus="initInputDom()"
          />
        </el-form-item>
      </el-form>

      <el-form
        v-show="active === 2"
        :model="questionAnswerForm"
        id="QA-from"
        ref="questionAnswerForm"
        :hide-required-asterisk="true"
      >
        <div v-for="(item, index) in questionAnswerForm.questionAnswerList" :key="index">
          <el-form-item :prop="item['question' + (index + 1)]" :label="$i18n.t('auth.question', { obj: index + 1 })">
            <el-select
              v-model="item['question' + (index + 1)]"
              :disabled="true"
              size="small"
              filterable
              allow-create
              clearable
              placeholder="Please select"
            >
              <el-option v-for="item in questionOptions" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>

          <el-form-item :prop="item['answer' + (index + 1)]" :label="$i18n.t('auth.answer', { obj: index + 1 })">
            <el-input
              size="small"
              type="password"
              v-model="item['answer' + (index + 1)]"
              placeholder="Please enter"
              @keyup.native.enter="nextFocus($event)"
              @focus="initInputDom()"
            >
            </el-input>
          </el-form-item>
        </div>
      </el-form>

      <el-button
        :disabled="maxTimes || !checkFormIptLth"
        class="login-btn"
        type="primary"
        v-preventReClick
        @click="submitForm()"
        :loading="loginLoading"
      >
        {{ active === 1 ? $i18n.t('auth.submit') : $i18n.t('auth.confirm') }}
        <i class="el-icon-right el-icon--right"></i>
      </el-button>

      <el-button type="text" class="return-btn" @click="returnEvent"> Return > </el-button>
    </div>
  </div>
</template>

<script>
import { inputRequiredValidateObj } from '@/common/validator/common';
import { queryOrgOperator, queryQuestions, verifyQuestion, resetPassword, queryOperatorQuestions } from '../../api';
export default {
  name: 'ForgetPassword',
  data() {
    return {
      loginErrorMsg: '',
      active: 1,
      forgetPwdForm: {
        shortCode: '',
        username: '',
      },
      questionAnswerForm: {
        questionAnswerList: [],
      },
      questionOptions: [],
      forgetPwdRules: {
        shortCode: [inputRequiredValidateObj],
        username: [inputRequiredValidateObj],
      },
      loginLoading: false,
      inputDoms: [],
      orgOperatorIdentityId: '',
      questionNumber: 0,
      maxTimes: false,
    };
  },
  computed: {
    checkFormIptLth() {
      let allEnterValue = true;
      if (this.active === 1) {
        for (let key in this.forgetPwdForm) {
          if (!this.forgetPwdForm[key]) {
            allEnterValue = false;
          }
        }
      } else {
        allEnterValue = this.questionAnswerForm.questionAnswerList.length > 0 ? true : false;
        for (let i = 0; i < this.questionAnswerForm.questionAnswerList.length; i++) {
          let item = this.questionAnswerForm.questionAnswerList[i];
          for (let key in item) {
            if (!item[key]) {
              allEnterValue = false;
            }
          }
        }
      }
      return allEnterValue;
    },
  },
  created() {},
  methods: {
    // 回显系统QA选项
    renderQuestionAnswer(number) {
      this.questionAnswerForm.questionAnswerList = [];
      for (let i = 0; i < number; i++) {
        let obj = {
          ['question' + (i + 1)]: '',
          ['answer' + (i + 1)]: '',
        };
        this.questionAnswerForm.questionAnswerList.push(obj);
      }
      this.getOperatorQuestions();
    },

    submitForm() {
      if (this.active === 1) {
        this.operatorValid();
      } else {
        this.questionValid();
      }
    },

    operatorValid() {
      this.$refs['forgetPwdForm'].validate((valid) => {
        if (valid) {
          this.loginLoading = true;
          let data = {
            ...this.forgetPwdForm,
          };
          queryOrgOperator(data)
            .then(async (res) => {
              if (res.header && res.header.success) {
                if (res.orgOperatorIdentityId) {
                  this.orgOperatorIdentityId = res.orgOperatorIdentityId || '';
                  let operatorQuestions = await this.queryOperatorQuestionsFn();
                  if (operatorQuestions.length > 0) {
                    this.getQuestions();
                    this.active = 2;
                    this.loginErrorMsg = '';
                  } else {
                    this.$confirm(this.$i18n.t('auth.notConfiguredTips'), {
                      confirmButtonText: 'Confirm',
                      showCancelButton: false,
                      type: 'warning',
                      customClass: 'forget-pwd-tips',
                    }).then(() => {});
                  }
                } else {
                  this.loginErrorMsg = 'The user does not exist.';
                }
              } else {
                this.loginErrorMsg = res.responseDesc || 'The user does not exist.';
              }
            })
            .finally(() => {
              this.loginLoading = false;
            });
        }
      });
    },

    // 获取系统设置的QA
    getQuestions() {
      this.questionOptions = [];
      queryQuestions().then((res) => {
        if (res.header && res.header.success) {
          let orgOperatorQuestionAnswer = res.orgOperatorQuestionAnswer;
          this.questionNumber = res.questionNumber || 0;
          if (orgOperatorQuestionAnswer.length > 0) {
            this.questionOptions = orgOperatorQuestionAnswer.map((item) => {
              return {
                value: item.question,
                label: item.question,
              };
            });
          }
          this.renderQuestionAnswer(this.questionNumber);
        }
      });
    },

    // 查询操作员已经设置的QA
    queryOperatorQuestionsFn() {
      let data = {
        initiatorIdentityId: this.orgOperatorIdentityId,
        receiverIdentityId: this.orgOperatorIdentityId,
      };
      return new Promise((resolve) => {
        queryOperatorQuestions(data).then((res) => {
          if (res.header && res.header.success) {
            let orgOperatorQuestionAnswer = res.orgOperatorQuestionAnswer;
            resolve(orgOperatorQuestionAnswer);
          }
        });
      });
    },

    // 回显操作员设置的QA选项
    async getOperatorQuestions() {
      let orgOperatorQuestionAnswer = await this.queryOperatorQuestionsFn();
      if (orgOperatorQuestionAnswer.length > 0) {
        for (let i = 0; i < orgOperatorQuestionAnswer.length; i++) {
          this.questionAnswerForm.questionAnswerList[i]['question' + (i + 1)] = orgOperatorQuestionAnswer[i].question;
        }
      }
    },

    questionValid() {
      this.$refs['questionAnswerForm'].validate((valid) => {
        if (valid) {
          this.loginLoading = true;
          let questionAnswer = this.questionAnswerForm.questionAnswerList.map((item, index) => {
            return {
              answer: item['answer' + (index + 1)],
              question: item['question' + (index + 1)],
            };
          });
          let data = {
            initiatorIdentityId: '',
            orgOperatorQuestionAnswer: questionAnswer,
            receiverIdentityId: this.orgOperatorIdentityId,
          };
          verifyQuestion(data)
            .then((res) => {
              if (res.header && res.header.success) {
                this.resetOrgPassword();
              } else {
                // 回答错误超过最大次数，抛出错误码
                if (res.header.responseCode === 'FE73302131') {
                  this.maxTimes = true;
                }
                this.loginErrorMsg = res.responseDesc || '';
              }
            })
            .finally(() => {
              this.loginLoading = false;
            });
        }
      });
    },

    resetOrgPassword() {
      let data = {
        initiatorIdentityId: this.orgOperatorIdentityId,
        receiverIdentityId: this.orgOperatorIdentityId,
      };
      resetPassword(data).then((res) => {
        if (res.header && res.header.success) {
          this.$message.success('Password retrieved successfully.');
          this.$router.push({ path: '/login' });
        } else {
          this.loginErrorMsg = res.responseDesc || '';
        }
      });
    },

    returnEvent() {
      if (this.active === 2) {
        this.maxTimes = false;
        this.active = 1;
      } else {
        this.$router.push({ path: '/login' });
      }
    },

    initInputDom() {
      if (this.inputDoms.length != 0) {
        return;
      }
      let allInputDoms = [];
      let inputDoms = document.querySelectorAll('#first-change-pwd .el-input__inner');
      let selectDoms = document.querySelectorAll('#QA-from .el-input__inner');
      allInputDoms = [...inputDoms, ...selectDoms];
      allInputDoms.forEach((item, index) => {
        item.setAttribute('data-index', index);
      });
      this.inputDoms = allInputDoms;
    },
    nextFocus(event) {
      let index = event.target.getAttribute('data-index');
      let nextIndex = parseInt(index) + 1;
      let length = this.inputDoms.length;
      if (nextIndex < length) {
        this.inputDoms[nextIndex].focus();
        return;
      }
      if (!this.loginLoading) {
        this.submitForm();
      }
    },
  },
};
</script>

