    p {
      margin: 4px 0;
      color: $--text_color--emphasis;
    }
    p:nth-of-type(1) {
      font-size: 20px;
      color: $--text_color--strong;
    }
    p:nth-of-type(2) {
      font-size: 36px;
      margin-bottom: 24px;
    }
  }
  /deep/ .el-step {
    margin-bottom: 20px;
    margin-top: 20px;
    .el-step__head.is-finish {
      color: $--primary_color;
      border-color: $--primary_color;
    }
    .is-finish {
      .el-step__line {
        background-color: #f0f0f0;
      }
      .el-step__icon {
        background-color: $--primary_color;
        border-color: $--primary_color;
        .el-step__icon-inner {
          color: #fff;
        }
      }
    }
    .el-step__title.is-finish {
      color: $--text_color--emphasis;
    }

    .is-process {
      .el-step__icon {
        background-color: $--fill_color--regular;
        border-color: $--fill_color--regular;
        .el-step__icon-inner {
          color: $--text_color--strong;
        }
      }
    }
    .el-step__title.is-process {
      font-weight: 400;
      color: $--line_color--strong;
    }
  }
  .error-tip-content {
    width: 100%;
    color: #f53f3f;
    background-color: rgba(255, 242, 240, 0.5);
    border-radius: 4px;
    line-height: 24px;
    padding-left: 12px;
    margin-bottom: 20px;
  }
  .login-btn {
    width: 100%;
    height: 40px;
    font-size: 16px;
    margin-top: 8px;
    .el-icon--right {
      float: right;
