<template>
  <div class="first-login-container">
    <div class="first-login-title">
      <p v-if="!isChangeWidthoutQA">{{ $i18n.t('auth.firstLoginChangePwdTitle') }}</p>
      <p v-else>{{ $i18n.t('auth.firstChangeWidthoutQA') }}</p>
    </div>

    <el-steps v-if="!isChangeWidthoutQA" :active="active" class="steps">
      <el-step title="Change Password"></el-step>
      <el-step title="Configure Question"></el-step>
    </el-steps>

    <div class="change-pwd-form">
      <el-form
        v-show="active === 1"
        :model="firstChangePwd"
        id="first-change-pwd"
        ref="firstChangePwd"
        :rules="firstChangePwdRules"
      >
        <el-form-item prop="oldPassword" :label="$i18n.t('auth.oldPassword')">
          <el-input
            size="small"
            type="password"
            v-model="firstChangePwd.oldPassword"
            placeholder="Please enter"
            @keyup.native.enter="nextFocus($event)"
            @focus="initInputDom()"
          >
            <svg-icon v-if="oldPasswordCheck" slot="suffix" icon-class="success" class="success-icon"></svg-icon>
          </el-input>
        </el-form-item>

        <el-form-item prop="newPassword" :label="$i18n.t('auth.newPassword')" required>
          <el-input
            size="small"
            type="password"
            v-model.trim="firstChangePwd.newPassword"
            @keyup.native.enter="nextFocus($event)"
            placeholder="Please enter"
            @focus="initInputDom('newPassword')"
          >
            <svg-icon v-if="newPasswordCheck" slot="suffix" icon-class="success" class="success-icon"></svg-icon>
          </el-input>
          <div v-show="showTips" class="newPassword-tips">
            {{ $t('home.newPwdTips', { obj: minimumLength + '~' + maximumLength }) }}
          </div>
        </el-form-item>

        <el-form-item prop="confirmPassword" :label="$i18n.t('auth.confirmPassword')">
          <el-input
            size="small"
            type="password"
            v-model="firstChangePwd.confirmPassword"
            placeholder="Please enter"
            @keyup.native.enter="nextFocus($event)"
            @focus="initInputDom()"
          >
            <svg-icon v-if="confirmPasswordCheck" slot="suffix" icon-class="success" class="success-icon"></svg-icon>
          </el-input>
        </el-form-item>
      </el-form>

      <el-form v-show="active === 2" :model="questionAnswerForm" id="QA-from" ref="questionAnswerForm">
        <div v-for="(item, index) in questionAnswerList" :key="index">
          <el-form-item
            :prop="'question' + (index + 1)"
            :label="$i18n.t('auth.question', { obj: index + 1 })"
            :rules="{ validator: duplicateQuestionVail, trigger: 'change' }"
            required
          >
            <el-select
              v-model="questionAnswerForm['question' + (index + 1)]"
              size="small"
              filterable
              allow-create
              clearable
              placeholder="Please select"
            >
              <el-option v-for="item in questionOptions" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>

          <el-form-item
            :prop="'answer' + (index + 1)"
            :label="$i18n.t('auth.answer', { obj: index + 1 })"
            :rules="{ validator: answerVail, trigger: 'blur' }"
            required
          >
            <el-input
              size="small"
              type="password"
              v-model="questionAnswerForm['answer' + (index + 1)]"
              placeholder="Please enter"
              @keyup.native.enter="nextFocus($event)"
              @focus="initInputDom()"
            >
            </el-input>
          </el-form-item>
        </div>
      </el-form>
    </div>

    <el-button
      :disabled="!checkFormIptLth"
      class="login-btn"
      type="primary"
      v-preventReClick
      @click="submitForm('loginForm')"
      :loading="loginLoading"
    >
      {{ active === 2 || isChangeWidthoutQA ? $i18n.t('auth.submit') : $i18n.t('auth.next') }}
      <i class="el-icon-right el-icon--right"></i>
    </el-button>

    <el-button type="text" class="return-btn" @click="returnEvent"> Return > </el-button>
  </div>
</template>

<script>
import { inputRequiredValidateObj } from '@/common/validator/common';
import { changePasswordOnFirstLogin, queryQuestions, passwordRule, changePassword } from '../../api';
import { authenticate } from '@/views/home/api';
import cookie from '@/common/utils/cookie';
export default {
  name: 'FirstLogin',
  props: {
    operatorName: {
      type: String,
      default: '',
    },
    shortCode: {
      type: String,
      default: '',
    },
  },
  data() {
    let newPasswordVail = async (rule, value, callback) => {
      if (value) {
        if (value.length < this.minimumLength || value.length > this.maximumLength) {
          this.newPasswordCheck = false;
          this.showTips = false;
          return callback(new Error(`Please enter a number between ${this.minimumLength} and ${this.maximumLength}.`));
        } else {
          this.showTips = false;
          this.newPasswordCheck = true;
          return callback();
        }
      } else {
        this.newPasswordCheck = false;
        this.showTips = false;
        callback(new Error('Please enter content'));
      }
    };
    let confirmPasswordVail = (rule, value, callback) => {
      if (value) {
        if (this.firstChangePwd.newPassword !== value) {
          this.confirmPasswordCheck = false;
          callback(new Error('The two passwords are inconsistent.'));
        } else {
          this.confirmPasswordCheck = true;
          callback();
        }
        callback();
      }
    };
    let oldPasswordVail = (rule, value, callback) => {
      if (value) {
        let data = {
          password: value,
          identityId: cookie.get('initiatorIdentityId'),
        };
        authenticate(data).then((res) => {
          if (res.header && res.header.success) {
            this.oldPasswordCheck = true;
            callback();
          } else {
            this.oldPasswordCheck = false;
            callback(new Error('Please Check The Password And Try Agin'));
          }
        });
      } else {
        this.oldPasswordCheck = false;
        callback(new Error('Please enter content'));
      }
    };
    return {
      active: 1,
      inputDoms: [],
      firstChangePwd: {
        oldPassword: '',
        newPassword: '',
        confirmPassword: '',
      },
      questionAnswerForm: {},
      questionAnswerList: [],
      firstChangePwdRules: {
        oldPassword: [{ validator: oldPasswordVail, trigger: 'blur' }],
        newPassword: [{ validator: newPasswordVail, trigger: 'blur' }],
        confirmPassword: [inputRequiredValidateObj, { validator: confirmPasswordVail, trigger: 'blur' }],
      },
      oldPasswordCheck: false,
      newPasswordCheck: false,
      confirmPasswordCheck: false,
      showTips: false,
      questionOptions: [],
      loginLoading: false,
      initiatorIdentityId: '',
      isFirstTips: true,
      isChangeWidthoutQA: false,
      answerMinLength: 0,
      sameAnswerFlag: 0,
      isQuestionLoad: false,
      minimumLength: 8,
      maximumLength: 20,
    };
  },
  computed: {
    checkFormIptLth() {
      let allEnterValue = false;
      if (this.active === 1) {
        if (
          this.firstChangePwd['oldPassword'] &&
          this.firstChangePwd['newPassword'] &&
          this.firstChangePwd['confirmPassword']
        ) {
          allEnterValue = true;
        }
      } else {
        allEnterValue = this.questionAnswerList.length > 0 ? true : false;
        for (let key in this.questionAnswerForm) {
          if (!this.questionAnswerForm[key]) {
            allEnterValue = false;
          }
        }
      }
      return allEnterValue;
    },
  },
  created() {
    this.initiatorIdentityId = this.$route.query.initiatorIdentityId;
    this.receiverIdentityId = this.$route.query.receiverIdentityId;
    this.isChangeWidthoutQA = this.$route.query.redirectFlag === 'changeWidthoutQA';
  },
  methods: {
    // 答案规则校验
    answerVail(rule, value, callback) {
      if (value) {
        if (value.length < this.answerMinLength) {
          callback(new Error(`Must be more than ${this.answerMinLength} characters`));
        } else {
          if (!this.sameAnswerFlag) {
            let answerArr = [];
            for (let key in this.questionAnswerForm) {
              if (key.indexOf('answer') !== -1) {
                answerArr.push(this.questionAnswerForm[key]);
              }
            }
            answerArr.sort();
            for (let i = 0; i < answerArr.length; i++) {
              if (answerArr[i] && answerArr[i] === answerArr[i + 1]) {
                callback(new Error('cannot configure the same answer'));
              }
            }
          }
        }
        callback();
      }
    },

    // 重复Question校验
    duplicateQuestionVail(rule, value, callback) {
      if (value) {
        let questionArr = [];
        for (let key in this.questionAnswerForm) {
          if (key.indexOf('question') !== -1) {
            questionArr.push(this.questionAnswerForm[key]);
          }
        }
        questionArr.sort();
        for (let i = 0; i < questionArr.length; i++) {
          if (questionArr[i] && questionArr[i] === questionArr[i + 1]) {
            callback(new Error('cannot configure the same question'));
          }
        }
      }
      callback();
    },

    // 回显系统QA数量与初始化QA表单
    renderQuestionAnswer(number) {
      this.questionAnswerList = [];
      for (let i = 0; i < number; i++) {
        let obj = {
          ['question' + (i + 1)]: '',
          ['answer' + (i + 1)]: '',
        };
        this.questionAnswerList.push(obj);
        this.$set(this.questionAnswerForm, 'question' + (i + 1), '');
        this.$set(this.questionAnswerForm, 'answer' + (i + 1), '');
      }
      this.isQuestionLoad = true;
    },

    returnEvent() {
      if (this.active === 2) {
        this.active = 1;
      } else {
        this.$router.go(-1);
      }
    },

    initInputDom(inputProp) {
      if (inputProp === 'newPassword' && this.isFirstTips) {
        passwordRule().then((res) => {
          if (res) {
            this.minimumLength = res.minimumLength;
            this.maximumLength = res.maximumLength;
          }
        });
        this.showTips = true;
        this.isFirstTips = false;
      }
      if (this.inputDoms.length != 0) {
        return;
      }
      let allInputDoms = [];
      let inputDoms = document.querySelectorAll('#first-change-pwd .el-input__inner');
      let selectDoms = document.querySelectorAll('#QA-from .el-input__inner');
      allInputDoms = [...inputDoms, ...selectDoms];
      allInputDoms.forEach((item, index) => {
        item.setAttribute('data-index', index);
      });
      this.inputDoms = allInputDoms;
    },
    nextFocus(event) {
      let index = event.target.getAttribute('data-index');
      let nextIndex = parseInt(index) + 1;
      let length = this.inputDoms.length;
      if (nextIndex < length) {
        this.inputDoms[nextIndex].focus();
        return;
      }
      if (!this.loginLoading) {
        this.submitForm();
      }
    },

    submitForm() {
      if (this.active === 1) {
        this.firstChangePwdValid();
      } else {
        this.questionAnswerValid();
      }
    },

    firstChangePwdValid() {
      this.$refs['firstChangePwd'].validate((valid) => {
        if (valid) {
          if (this.isChangeWidthoutQA) {
            this.questionAnswerValid();
          } else {
            this.active = 2;
            if (!this.isQuestionLoad) {
              this.getQuestions();
            }
          }
        }
      });
    },

    getQuestions() {
      this.questionOptions = [];
      queryQuestions().then((res) => {
        if (res.header && res.header.success) {
          let orgOperatorQuestionAnswer = res.orgOperatorQuestionAnswer;
          this.answerMinLength = res.questionPolicy.answerMinLength;
          this.sameAnswerFlag = res.questionPolicy.sameAnswerFlag || 0;
          let questionNumber = res.questionPolicy.questionNumber || 0;
          if (orgOperatorQuestionAnswer.length > 0) {
            this.questionOptions = orgOperatorQuestionAnswer.map((item) => {
              return {
                value: item.question,
                label: item.question,
              };
            });
          }
          this.renderQuestionAnswer(questionNumber);
        }
      });
    },

    questionAnswerValid() {
      this.$refs['questionAnswerForm'].validate((valid) => {
        if (valid) {
          let data = {
            operatorName: this.operatorName,
            shortCode: this.shortCode,
            confirmPassword: this.firstChangePwd.confirmPassword,
            initiatorIdentityId: this.initiatorIdentityId,
            receiverIdentityId: this.initiatorIdentityId,
          };
          if (!this.isChangeWidthoutQA) {
            data.newPassword = this.firstChangePwd.newPassword;
            data.oldPassword = this.firstChangePwd.oldPassword;
            let questionAnswer = this.questionAnswerList.map((item, index) => {
              return {
                answer: this.questionAnswerForm['answer' + (index + 1)],
                question: this.questionAnswerForm['question' + (index + 1)],
              };
            });
            data.orgOperatorQuestionAnswer = questionAnswer;
            this.changePwdOnFirstLoginWidthQA(data);
          } else {
            data.newPwd = this.firstChangePwd.newPassword;
            data.oldPwd = this.firstChangePwd.oldPassword;
            this.changePwdWidthoutQA(data);
          }
        }
      });
    },

    changePwdOnFirstLoginWidthQA(data) {
      this.loginLoading = true;
      changePasswordOnFirstLogin(data)
        .then((res) => {
          if (res && res.header.success) {
            this.$message.success('Change password success!');
            this.$router.push({
              path: '/login',
              query: {
                operatorName: this.operatorName,
              },
            });
          } else {
            this.$message.error(res.header.responseDesc || 'Change password failed!');
          }
        })
        .catch((err) => {
          this.$message.error(err.message);
        })
        .finally(() => {
          this.loginLoading = false;
        });
    },

    changePwdWidthoutQA(data) {
      this.loginLoading = true;
      changePassword(data)
        .then((res) => {
          if (res.header && res.header.success) {
            this.$message.success('Change password success!');
            this.$router.push({
              path: '/login',
              query: {
                operatorName: this.operatorName,
              },
            });
          } else {
            this.$message.error(res.header.responseDesc || 'Change password failed!');
          }
        })
        .catch((err) => {
          this.$message.error(err.message);
        })
        .finally(() => {
          this.loginLoading = false;
        });
    },
  },
};
</script>

<style lang="scss" scoped>
