  max-width: 480px;
  margin: 0 auto;
  align-self: center;
  /deep/ .el-form {
    .el-form-item {
      .el-input__inner {
        color: $--text_color--emphasis;
      }
    }
  }
  .steps {
    width: 300px;
  }
