    color: $--text_color--secondary;
  }

  /deep/ .el-step {
    margin-bottom: 20px;
    margin-top: 20px;
    .el-step__head.is-finish {
      color: $--primary_color;
      border-color: $--primary_color;
    }
    .is-finish {
      .el-step__line {
        background-color: #f0f0f0;
      }
      .el-step__icon {
        background-color: $--primary_color;
        border-color: $--primary_color;
        .el-step__icon-inner {
          color: #fff;
        }
      }
    }
    .el-step__title.is-finish {
      color: $--text_color--emphasis;
    }

    .is-process {
      .el-step__icon {
        background-color: $--fill_color--regular;
        border-color: $--fill_color--regular;
        .el-step__icon-inner {
          color: $--text_color--strong;
        }
      }
    }
    .el-step__title.is-process {
      font-weight: 400;
      color: $--text_color--secondary;
    }
  }

  .change-pwd-form {
    /deep/.el-form-item__label {
      font-size: 14px;
