      color: $--fill_color--emphasis;
    }
    .el-select {
      width: 100%;
    }
    .el-form-item {
      margin-bottom: 14px;
      /deep/ .el-form-item__content {
        .el-select:hover {
          .el-input__inner {
            border-color: $--line_color--strong;
          }
        }
        .el-icon-circle-close {
          top: -2px;
        }
      }
    }
    .newPassword-tips {
      font-size: 12px;
