    }
  }

  .login-btn {
    width: 100%;
    height: 40px;
    font-size: 16px;
    margin-top: 8px;
    .el-icon--right {
      float: right;
      position: relative;
      top: -2px;
    }
  }

  .return-btn {
    margin-left: 0px;
    margin-top: 12px;
    color: $--text_color--strong;
  }
}
</style>
